<template>
  <div id="user" @touchstart="hideList">
    <div id="main">
      <p>个人中心</p>
      <p>公司事务管理平台</p>
    </div>
    <div class="main-content">
      <div class="main-function">
        <transition-group name="funcList">
          <div v-for="(item, index) in showList" v-bind:key="index" class="func-item left" :class="{'head': index === 0}" @click="choose(index)">
            <p class="left">{{item}}</p>
            <div v-show="funcStatus && index === 0" class="back pointer left" @click="back"></div>
          </div>
        </transition-group>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import {checkCookie} from '../../resource/js/cache.js'
export default {
  name: 'user',
  data () {
    return {
      funcList: [
        '所有应用',
        '报销',
        '审批',
        '新闻管理',
        '产品管理',
        '信息管理',
        '权限管理',
        '退出'
      ],
      showList: [],
      funcIndex: -1,
      funcStatus: false,
      uploadStatus: false,
      carouselList: [
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者'
      ],
      newsList: [
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者',
        '华为FusionCube跃居超融合挑战者'
      ]
    }
  },
  methods: {
    hideList: function () {
      this.$root.eventHub.$emit('hide')
    },
    choose: function (i) {
      this.funcIndex = i
      switch (i) {
        case 1:
          this.$router.push({path: '/user/reimbursement'})
          break
        case 2:
          this.$router.push({path: '/user/accounting'})
          break
        case 3:
          this.$router.push({path: '/user/news'})
          break
        case 4:
          this.$router.push({path: '/user/products'})
          break
        case 5:
          this.$router.push({path: '/user/setting'})
          break
        case 6:
          this.$router.push({path: '/user/authority'})
          break
        case 7:
          this.$root.eventHub.$emit('clear')
          this.$router.push({path: '/home'})
          break
      }
      // if (i !== 0 && i !== this.funcList.length - 1) {
      //   this.funcStatus = true
      //   this.funcIndex = i
      //   this.showList = new Array(this.funcList[i])
      // } else if (i === this.funcList.length - 1) {
      //   this.$router.push({path: '/'})
      //   this.$root.eventHub.$emit('clear')
      // }
    },
    back: function () {
      this.funcIndex = -1
      if (this.$route.name === 'NewsUpload') {
        this.$root.eventHub.$emit('cancelUpload')
        this.$router.push({path: '/user/news'})
      } else if (this.$route.name === 'ReApplication' || this.$route.name === 'ReForm') {
        this.$root.eventHub.$emit('cancelReimbursement')
        this.$router.push({path: '/user/reimbursement'})
      } else {
        this.$router.push({path: '/user'})
      }
      // this.showList = this.funcList
      // if (!this.uploadStatus) {
      //   event.stopPropagation()
      //   this.funcStatus = false
      //   this.showList = this.funcList
      //   this.funcIndex = -1
      //   this.showCarousel = false
      //   this.showAllNews = false
      // } else {
      //   this.uploadStatus = false
      //   this.funcIndex = 3
      //   this.showList = new Array(this.funcList[3])
      // }
    },
    manageRoute: function (x) {
      switch (x) {
        case 'user':
          this.funcStatus = false
          this.showList = this.funcList
          break
        case 'Reimbursement':
          this.funcStatus = true
          this.showList = new Array('报销')
          break
        case 'ReApplication':
          this.funcStatus = true
          this.$root.eventHub.$emit('Reimbursement')
          this.showList = new Array('申请')
          break
        case 'ReForm':
          this.funcStatus = true
          this.$root.eventHub.$emit('Reimbursement')
          this.showList = new Array('汇总')
          break
        case 'Accounting':
          this.funcStatus = true
          this.showList = new Array('审批')
          break
        case 'NewsManage':
          this.funcStatus = true
          this.showList = new Array('新闻管理')
          break
        // eslint-disable-next-line no-duplicate-case
        case 'NewsUpload':
          this.funcStatus = true
          this.$root.eventHub.$emit('Upload')
          this.showList = new Array('新闻上传')
          break
        case 'ProductsManage':
          this.funcStatus = true
          this.showList = new Array('产品管理')
          break
        case 'Setting':
          this.funcStatus = true
          this.showList = new Array('信息管理')
          break
        case 'Authority':
          this.funcStatus = true
          this.showList = new Array('权限管理')
      }
    }
  },
  watch: {
    '$route': function (to, from) {
      this.manageRoute(to.name)
      if (to.name === 'NewsManage' && from.name === 'NewsUpload') {
        this.$root.eventHub.$emit('cancelUpload')
      } else if (to.name === 'Reimbursement' && from.name === 'ReApplication') {
        this.$root.eventHub.$emit('cancelReimbursement')
      } else if (to.name === 'Reimbursement' && from.name === 'ReForm') {
        this.$root.eventHub.$emit('cancelReimbursement')
      }
    }
  },
  mounted () {
    $('::-webkit-scrollbar').css('display', 'none')
    if (checkCookie(2, '')) {
      this.showList = this.funcList
    } else {
      this.$router.push({path: '/sign'})
    }
    if (!this.$root.adapt) {
      var temp = this.$root.carousel
      var text = (temp - ($('#main p:first-of-type').height() + $('#main p:last-of-type').height())) / 2
      $('#main').css('height', temp)
      $('#main p:first-of-type').css('margin-top', text)
      $('.nav-bar').css('position', 'absolute')
    }
    this.manageRoute(this.$route.name)
  },
  destroyed () {
    if (!this.$root.adapt) {
      $('.nav-bar').css('position', 'fixed')
    }
  }
}
</script>

<style scoped>
  #main{
    background-size: cover;
    overflow: hidden;
    position: relative;
    background: #3CB371;
  }
  #main p{
    text-align: center;
    color: white;
    line-height: 140%;
  }
  .main-content{
    height: auto;
    overflow: hidden;
  }
  .funcList-enter-active, .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .funcList-enter, .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  .func-item{
    border-bottom: 1px solid #f0f2f5;
    background: white;
    overflow: hidden;
    position: relative;
    z-index: 3;
  }
  .func-item.head{
    z-index: 5;
  }
  .func-item:not(:last-of-type){
    border-bottom: 1px solid #f0f2f5;
  }
  .func-item p{
    font-size: 18px;
  }
  .func-item.head p{
    font-size: 18px;
    font-weight: bold;
  }
  .back{
    background: url("../../resource/assets/back2.png") 50% no-repeat;
    background-size: 50%;
  }

  @media only screen and (max-device-width: 767px) {
    #main {
      width: 100%;
    }
    #main p:first-of-type{
      height: 48px;
      padding: 0 40px;
      font-size: 24px;
    }
    #main p:nth-of-type(2){
      padding: 0 30px;
      font-size: 22px;
    }
    .main-content{
      width: 100%;
    }
    .func-item{
      width: calc(100% - 40px);
      height: 41px;
      margin: 0 20px;
      padding: 8px 0;
    }
    .func-item p{
      line-height: 41px;
    }
    .back{
      width: 41px;
      height: 41px;
      float: right;
    }
  }

  @media only screen and (min-device-width: 768px) {
    #main{
      width: 100%;
      height: 540px;
    }
    #main p:first-of-type{
      margin-top: 220px;
      padding: 0 40px;
      font-size: 32px;
    }
    #main p:nth-of-type(2){
      margin-top: 24px;
      padding: 0 30px;
      font-size: 22px;
    }
    .main-content{
      padding-bottom: 18px;
    }
    .main-content>div:nth-of-type(2){
      padding-bottom: 36px;
    }
    .main-function{
      width: 100%;
      height: auto;
      overflow: hidden;
      position: relative;
      z-index: 8;
    }
    .func-item{
      width: calc(50% - 76px);
      height: 39px;
      margin: 0 18px;
      padding: 10px 20px;
    }
    .func-item.head{
      width: calc(100% - 76px);
      padding: 30px 20px 10px;
      background: white;
    }
    .func-item:not(.head){
      cursor: pointer;
    }
    .func-item p{
      line-height: 39px;
    }
    .back{
      width: 39px;
      height: 39px;
      margin-left: 10px;
    }
  }

  @media only screen and (min-device-width: 768px) and (max-width: 600px) {
    .func-item{
      width: calc(100% - 76px);
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1094px) {
    #main p {
      width: 100vw;
    }
    .main-content{
      width: 1066px;
      margin: 0 calc((100vw - 1066px) / 2);
    }
  }

  @media only screen and (min-device-width: 768px) and (min-height: 960px) {
    #main{
      height: 600px;
    }
    #main p:first-of-type{
      margin-top: 250px;
    }
  }
</style>



